:root {
  --color-main: #2c5854;
  --color-secondary: #e4a104;
  --bg-body: #f8f8fb;
  --main-accent: #8492a6;
  --main-text: #8492a6;
  --success: #28c745;
  --danger: #c70909;
  --shadow: rgba(224, 208, 208, 0.15) 0px 2px 8px;
}

* {
  font-family: 'Poppins', sans-serif;
  text-decoration: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  color: #8492a6;
  color: var(--main-text);
  background: #f8f8fb;
  background: var(--bg-body);
  overflow-x: hidden;
}

.brandimage {
  height: 100px;
  width: 100px;
}

#menu-toggle {
  display: none;
}

#menu-toggle:checked ~ .sidebar {
  display: none;
}

#menu-toggle:checked .main-content .engage-card {
  max-width: 698px;
}

#menu-toggle:checked .main-content .emails-card {
  max-width: 344px;
}

.page-wrapper {
  width: 100%;
  margin: auto;
  display: flex;
}

.page-wrapper::before {
  content: '';
  width: 100%;
  max-width: 100%;
  height: 430px;
  position: absolute;
  left: 0;
  top: 0;
  background: #2c5854;
  background: var(--color-main);
  z-index: -1;
  border-radius: 0px 0px 0px 150px;
}

.sidebar {
  width: 250px;
  margin-left: 30px;
  z-index: 10;
}

.brand {
  height: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding-left: 0.2rem; */
  text-align: center;

  /* JC and TA removed */
}

.brand h3 {
  font-size: 1.8rem;
  color: #fff;
}

.profile-card {
  text-align: center;
  margin: 0.1rem 0rem 0rem 0rem;
}

/* .profile-img {
  height: 100px;
  width: 100px;
  border-radius: 50%;
  margin: auto;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 1.3rem;
} */

.profile-info {
  color: #fff;
  margin-bottom: 1rem;
  margin-top: 3rem;
}

.profile-info h2 {
  margin-bottom: 0.3rem;
}

.profile-info small {
  font-size: 1rem;
}

.profile-action {
  margin-bottom: 1.8rem;
}

.btn {
  border: none;
  outline: none;
  border-radius: 20px;
  padding: 0.6rem 2rem;
  display: flex;
  align-items: center;
}

.btn-white {
  background: #fff;
}

.btn span {
  font-size: 1.5rem;
  display: inline-block;
  margin-right: 0.6rem;
}
.profile-icons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 3.5rem;
}

.profile-icons span {
  display: inline-block;
  color: #fff;
  font-size: 1.7rem;
}

.sidebar-menu {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 2rem;
  margin-top: 5rem;
}

.menu-item a {
  display: block;
  width: 100%;
  padding: 1rem 0rem;
  border-radius: 10px;
}

.menu-item span {
  display: block;
  text-align: center;
  color: #8492a6;
  color: var(--main-text);
  font-size: 1rem;
}

.icon {
  margin-top: 2rem;
}

.menu-item a span:first-child {
  font-size: 2rem;
  margin-bottom: 0.7rem;
}

.menu-item a.active {
  background: #e48365;
}

.menu-item a.active span {
  color: rgb(3, 2, 2);
}

.menu-item span:hover {
  color: #599691c5;
}

.menu-item:hover {
  cursor: pointer;
}

.sidebar-card {
  padding: 1rem;
  border-radius: 10px;
  background: #e4a104;
  background: var(--color-secondary);
  margin-top: 2rem;
  color: #fff;
}
.sidebar-card h3 {
  margin-bottom: 1rem;
  font-size: 1.4rem;
}

.sidebar-card p {
  line-height: 1.9rem;
  margin-bottom: 1rem;
  font-size: 0.9rem;
  text-align: justify;
}

.sidebar-card a {
  text-align: center;
  font-weight: 600;
  color: #8492a6;
  color: var(--main-text);
  display: block;
  font-size: 1rem;
}

.main-content {
  padding: 0 40px 0px 60px;
  flex: 1 1;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  border-bottom: 1px solid #eee;
  color: #fff;
}

header label {
  display: inline-block;
  font-size: 1.4rem;
}

.head-icons {
  display: flex;
  align-items: center;
}

.head-icons span {
  margin-left: 0.5rem;
  display: inline-block;
  font-size: 1.2rem;
}

.head-avatar .avatar {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  background-size: cover;
  background-repeat: no-repeat;
}

.head-avatar span {
  margin-right: 0;
  padding: 0rem 0.5rem;
  font-size: 0.85rem;
  font-weight: 600;
}

.page-header {
  color: #fff;
  margin: 1rem 0rem;
}
.analytics {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 1rem;
  /* margin-top: 2rem; */
}

.card {
  background: #fff;
  border-radius: 10px;
  box-shadow: rgba(224, 208, 208, 0.15) 0px 2px 8px;
  box-shadow: var(--shadow);
  padding: 1rem;
}

.card-head {
  font-size: 1.2rem;
  color: #555;
  font-weight: 600;
  padding: 0 2rem;
}

.card-body {
  padding: 1.5rem 2rem;
}

.card-footer {
  padding: 1rem;
}

/* .engage-card .emails-card{
   min-width: 100%;
} */

.emails-card .card-body {
  padding: 0.3rem;
}

.emails-card,
.welcome {
  height: 80vh;
}

.chart-wrapper {
  padding-top: 1rem 0rem;
}

.emails-info {
  /* display: grid;
    grid-template-columns: repeat(2, 1fr); */
  /* grid-gap: 1rem; */
  padding-bottom: 2rem;
}

.bg-success {
  background: #28c745;
  background: var(--success);
  color: #fff;
}

.bg-danger {
  background: #c70909;
  background: var(--danger);
  color: #fff;
}

.bg-warning {
  background: #e4a104;
  background: var(--color-secondary);
  color: #fff;
}

.email-stat {
  display: flex;
  align-items: center;
}

.email-stat span {
  display: inline-block;
}

.email-stat .status {
  height: 15px;
  width: 15px;
  display: inline-block;
  border-radius: 50%;
  margin-right: 0.5rem;
}

table tr td,
table tr th {
  text-align: center;
  padding: 1.2rem 1rem;
}

table tr th {
  text-transform: uppercase;
  font-size: 1rem;
}

table {
  width: 100%;
  border-collapse: collapse;
}

table tr {
  border-bottom: 1px solid #efefef;
}

table h4,
table p {
  color: #333;
  margin-bottom: 0.5rem;
}

.grid-2 {
  margin-top: 3.5rem;
  display: grid;
  grid-template-columns: 65% auto;
  grid-gap: 2rem;
}

.team-info {
  display: flex;
  align-items: center;
}

.team-progress h4 {
  color: #8492a6;
  color: var(--main-text);
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.progress-bar {
  width: 100%;
  background: #efefef;
  position: relative;
  border-radius: 10px;
  height: 20px;
}

.indicator {
  height: 100%;
}

.indicator.success {
  background: #28c745;
  background: var(--success);
  border-radius: 10px;
}

.indicator.warning {
  background: #8492a6;
  background: var(--main-accent);
  border-radius: 10px;
}

.indicator.danger {
  background: #c70909;
  background: var(--danger);
  border-radius: 10px;
}

.btn-main {
  color: white;
  background: #e4a104;
  background: var(--color-secondary);
}

@media only screen and (max-width: 1280px) {
  .sidebar {
    display: none;
  }

  #menu-toggle:checked ~ .sidebar {
    display: block;
  }
}

@media only screen and (max-width: 980px) {
  .main-content {
    padding: 0 20px;
    flex: 1 1;
  }

  .analytics,
  .grid-2 {
    grid-template-columns: 100%;
  }
}

@media only screen and (max-width: 640px) {
  .page-wrapper::before {
    min-width: 600px;
  }
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

